import * as THREE from "three";
import { useGLTF } from "@react-three/drei";

export default function Building() {
	const { nodes, materials } = useGLTF("projects/trox/models/troxDemo.glb");
	const debug = false; // TODO: Replace with global debug setting

	return (
		<group dispose={null}>
			{Object.keys(nodes).map((key) => {
				const node = nodes[key];
				if (node.type === "Mesh") {
					if (debug)
						console.log(`Node: ${key}`, {
							position: node.position,
							rotation: node.rotation,
							scale: node.scale,
						});

					const material = (node as THREE.Mesh).material;
					const materialName = Array.isArray(material) ? material[0].name : material.name;

					return (
						<mesh
							name={node.name}
							key={key}
							geometry={(node as THREE.Mesh).geometry}
							material={materials[materialName]}
							position={node.position}
							rotation={node.rotation}
							scale={node.scale}
							castShadow={materialName !== "Glass" && materialName !== "Ground"}
							receiveShadow={materialName !== "Glass"}
						/>
					);
				}
				return null;
			})}
		</group>
	);
}
